import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { TranslateModule, TranslateService } from '@ngx-translate/core';

import { LanguageService } from './services/language.service';
import { TranslationLoaderService } from './services/translation-loader.service';

import { AppComponent } from './app.component';
import {
  PersonalInfoComponent,
  InterestsComponent
} from './components/sections';
import {
  ContactWithImgComponent,
  SectionHeaderComponent
} from './components/common';
import { LanguageComponent } from './components/sections/language/language.component';
import { EducationComponent } from './components/sections/education/education.component';
import { SkillsAndToolsComponent } from './components/sections/skills-and-tools/skills-and-tools.component';
import { WorkExperienceComponent } from './components/sections/work-experience/work-experience.component';
import { CarrerSummaryComponent } from './components/sections/carrer-summary/carrer-summary.component';

@NgModule({
  declarations: [
    AppComponent,
    PersonalInfoComponent,
    ContactWithImgComponent,
    InterestsComponent,
    SectionHeaderComponent,
    LanguageComponent,
    EducationComponent,
    SkillsAndToolsComponent,
    WorkExperienceComponent,
    CarrerSummaryComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    TranslateModule.forRoot()
  ],
  providers: [
    TranslateService,
    LanguageService,
    TranslationLoaderService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
